@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap");
body {
  font-family: 'Poppins', sans-serif;
  text-align: justify;
  margin-left: 0%;
  margin-right: 0%;
}

body h2 {
  letter-spacing: 1px;
}

header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

header img {
  max-height: 120px;
}

header nav {
  width: 200px;
  display: inline;
}

header nav a {
  margin-left: 5%;
  color: black;
  text-decoration: none;
}

nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

nav button {
  border: none;
  padding: 2%;
  background-color: white;
}

nav :hover {
  background-color: #fdc07a;
  cursor: pointer;
}

section {
  padding: 3%;
}

.bedrooms {
  width: 80%;
  margin-left: 35px;
  padding-top: 10px;
}

figure {
  width: 30%;
  background-color: white;
  -webkit-box-shadow: 5px 5px 5px #4d4d4e;
          box-shadow: 5px 5px 5px #4d4d4e;
}

figcaption {
  text-align: center;
  padding: 10px 20px;
}

.rooms {
  background-color: #ffd4ad;
  padding: 5%;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
}

.overview {
  padding: 5%;
  margin-left: 0%;
  background-color: #fdc07a;
}

.sectionLayout {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}

.sectionLayout p {
  padding-left: 5%;
  padding-right: 5%;
  margin: 0%;
}

.sectionLayout img {
  max-height: 190px;
  margin-bottom: 3%;
  border-radius: 10%;
}

footer {
  text-align: center;
  padding-top: 2%;
}

footer .footerLayout {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

footer .footerLayout .fContentLayout {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding: 3%;
  max-width: 200px;
}

.user-data-holder {
  margin-left: 45%;
  margin-top: -25%;
  position: absolute;
  background-color: white;
  width: 30%;
  -webkit-box-shadow: 5px 5px 5px #4d4d4e;
          box-shadow: 5px 5px 5px #4d4d4e;
  border-radius: 5%;
  text-align: center;
}

.logout {
  border: none;
  font-size: 16px;
  width: 100px;
}

.logout :hover {
  background-color: #fdc07a;
  cursor: pointer;
}

@media screen and (max-width: 400px) {
  header img {
    max-height: 200px;
  }
  .sectionLayout p {
    padding-top: 60%;
    padding-left: 0%;
    padding-right: 0%;
    margin-left: -270px;
  }
  .bedrooms {
    width: 100px;
  }
  figure {
    width: 120px;
    background-color: white;
    -webkit-box-shadow: 5px 5px 5px #4d4d4e;
            box-shadow: 5px 5px 5px #4d4d4e;
    margin-top: 25%;
  }
  #deluxe {
    margin-left: -40%;
  }
  footer {
    text-align: center;
    padding-top: 2%;
  }
  footer .footerLayout {
    width: 250px;
  }
  footer .footerLayout .fContentLayout {
    width: 150px;
    padding: 5px;
    margin-left: 0%;
  }
  footer .footerLayout .fContentLayout h2 {
    font-size: 12px;
  }
  footer .footerLayout .fContentLayout p {
    width: 130px;
    height: auto;
    font-size: 12px;
  }
}
